<template>
  <div
    class="pj-card pointer"
    :class="showHover && 'isHover'"
    @mouseover="showHover = true"
    @mouseleave="showHover = false"
  >
    <div class="img-box">
      <img v-lazy="project.featuredImage" alt="" />
    </div>
    <slide-y-down-transition :duration="500">
      <div class="text-box" v-if="showHover">
        <h5>{{ project.name }}</h5>
        <p class="mb-0" v-html="project.descriptions"></p>
      </div>
    </slide-y-down-transition>
  </div>
</template>

<script>
export default {
  props: {
    project: {},
  },
  data() {
    return {
      showHover: false,
    };
  },
  methods: {
    mouseLeave() {
      this.showHover = false;
    },
  },
};
</script>

<style scoped>
.img-box {
  padding: 30px;
  background: rgba(var(--vs-primary), 0.2);
  border-top-right-radius: 25px;
  border-bottom-left-radius: 25px;
  border-top-left-radius: 15px;
  border-bottom-right-radius: 15px;
  transition: 0.25s all linear;
}
.isHover .img-box {
  border-top-right-radius: 40px;
  border-bottom-left-radius: 40px;
}
.isHover img {
  transform: scale(1.2) !important;
}
.pj-card .text-box {
  position: absolute;
  top: 150px;
  background: rgba(var(--vs-background-2), 0.7);
  backdrop-filter: blur(20px);
  z-index: 100;
  padding: 15px;
  border-radius: 15px;
  width: 80%;
  margin-left: 5%;
  box-shadow: 0px 4px 25px 0px rgb(0 0 0 / 10%);
}

.pj-card img {
  transform: scale(1);
  transition: 0.2s all linear;
  width: 100%;
  max-height: 180px;
  object-fit: contain;
}
</style>
