          transform="translate(0.000000, -512.000000)"
          fill="rgba(var(--vs-background-2),1)"
        >
          <path
            d="M457.882303,684.481474 C769.796559,737.701171 1037.4375,631.794123 1270.23044,386.483964 L1356.3452,536.623647 L109.447182,1256.52055 L55.5057762,1163.0913 C294.60816,1013.67839 104.493247,624.185229 457.882303,684.481474 Z"
