        </div>
      </div>
    </div>
    <div class="col-12 section-3 pt-5" id="section-3">
      <div class="w-100 text-center">
        <h1 data-aos="fade-right" data-aos-offset="100" data-aos-duration="800">
          Projects
        </h1>
        <div
          data-aos="fade-right"
          data-aos-offset="100"
          data-aos-duration="800"
          data-aos-delay="100"
          class="my-4 justify-content-center d-flex align-items-center"
        >
          <div class="circle"></div>
          <div class="line"></div>
        </div>
      </div>
      <div
        class="row px-5"
        data-aos="fade-up"
        data-aos-offset="100"
        data-aos-duration="800"
      >
        <div
          class="col-12 px-0 header-bar mb-4 d-flex align-items-center justify-content-center"
        >
          <vs-button
            transparent
            class="mr-2"
            :active="activeProject === 'All'"
            @click="activeProject = 'All'"
            :size="'large'"
          >
            <i class="bx bx-grid-alt mr-2"></i> All</vs-button
          >
          <vs-button
            transparent
            class="mr-2"
            :active="activeProject === 'work'"
            @click="activeProject = 'work'"
            :size="'large'"
            ><i class="bx bx-briefcase-alt mr-2"></i>Work</vs-button
          >
          <vs-button
            transparent
            class="mr-2"
            :active="activeProject === 'freelance'"
            @click="activeProject = 'freelance'"
            :size="'large'"
            ><i class="bx bx-user mr-2"></i> Freelance</vs-button
          >
          <vs-button
            transparent
            class="mr-2"
            :active="activeProject === 'training'"
            @click="activeProject = 'training'"
            :size="'large'"
            ><i class="bx bx-target-lock mr-2"></i> Training</vs-button
          >
        </div>
        <div
          class="col-3 mb-3"
          data-aos="zoom-in-up"
          data-aos-offset="100"
          data-aos-duration="300"
          :data-aos-delay="n * 100"
          v-for="(project, n) in projects"
          :key="project.name"
          @click="seeMorePJ(project)"
        >
          <Card :project="project" class="mb-3" />
        </div>

        <div class="col-12 d-flex align-items-center justify-content-center">
          <vs-button
            :size="'large'"
            data-aos="zoom-in-up"
            data-aos-offset="100"
            data-aos-duration="800"
            active
            blank
            href="https://github.com/saihtunlu"
          >
            See more in GitHub
            <i class="bx ml-2 bx-link-external"></i
          ></vs-button>
        </div>
      </div>
    </div>
    <div class="w-100" style="background: rgba(var(--vs-background-2))">
      <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320">
        <path
          fill="#6a53d6"
          fill-opacity="1"
          d="M0,64L40,106.7C80,149,160,235,240,272C320,309,400,299,480,261.3C560,224,640,160,720,133.3C800,107,880,117,960,154.7C1040,192,1120,256,1200,256C1280,256,1360,192,1400,160L1440,128L1440,320L1400,320C1360,320,1280,320,1200,320C1120,320,1040,320,960,320C880,320,800,320,720,320C640,320,560,320,480,320C400,320,320,320,240,320C160,320,80,320,40,320L0,320Z"
        ></path>
      </svg>
    </div>

    <div class="col-12 section-4 pt-5 primary-bg" id="section-4">
      <div class="app-circle-1"></div>
      <div class="app-rectangle-1"></div>
      <div class="app-rectangle-2"></div>
      <form
        class="contact-form"
        data-aos="zoom-in-up"
        data-aos-duration="800"
        @submit.prevent="sendEmail"
      >
        <h3 class="text-center">Contact Me</h3>
        <label>Name</label>
        <input type="text" name="user_name" />
        <label>Email</label>
        <input type="email" name="user_email" />
        <label>Message</label>
        <textarea
          name="message"
          style="min-height: 120px; margin-bottom: 30px"
        ></textarea>
        <vs-button dark shadow type="submit" size="large" :loading="isLoading"
          >Send</vs-button
        >
      </form>
      <div class="w-100 d-flex align-items-center justify-content-between">
        <p class="d-flex align-items-center">
          COPYRIGHT ©2020 Sai HL, All rights Reserved
        </p>
        <p class="d-flex align-items-center">
