          <i
            class="bx bx-heart ml-2"
            style="color: rgb(var(--vs-warn)); font-size: 18px !important"
          ></i>
        </p>
      </div>
    </div>

    <vs-dialog scroll overflow-hidden blur not-close v-model="active">
      <template #header>
        <h3>{{ selectedProject.name }}</h3>
      </template>
      <div class="con-content">
        <p class="font-weight-bold">Preview</p>
        <hooper class="mb-3" :wheelControl="false" :itemsToShow="3">
          <slide
            v-for="(src, imageIndex) in selectedProject.images"
            :key="imageIndex"
            class="mr-3"
          >
            <img
              class="w-100 pointer"
              @click="index = imageIndex"
              v-lazy="src"
              v-if="src.includes('image')"
            />
            <video
              :src="src"
              style="
                width: 100%;
                margin-right: auto;
                margin-left: auto;
                display: block;
                border-radius: 15px;
                overflow: hidden;
              "
              controls
              v-if="src.includes('video')"
              class="mb-3"
            ></video>
          </slide>
          <hooper-navigation slot="hooper-addons"></hooper-navigation>
        </hooper>
