        <p v-html="selectedProject.descriptions"></p>
        <p>
          <span class="font-weight-bold">Features</span><br />
          <span v-for="feature in selectedProject.features" :key="feature">
            <span>- <span v-html="feature"></span></span><br />
          </span>
        </p>
      </div>
    </vs-dialog>
    <CoolLightBox
      v-if="active"
      :items="selectedProject.images"
      :useZoomBar="true"
      :index="index"
      @close="index = null"
    >
    </CoolLightBox>
    <slide-y-down-transition :duration="300">
      <vs-button dark shadow class="back-top" v-if="showBackToTop" @click="scrollToTop"
        ><i class="bx bx-chevron-up"></i
      ></vs-button>
    </slide-y-down-transition>
  </div>
</template>

<script>
import { Hooper, Slide, Navigation as HooperNavigation } from "hooper";
import AOS from "aos";
import "aos/dist/aos.css";
import "hooper/dist/hooper.css";
import CoolLightBox from "vue-cool-lightbox";
import "vue-cool-lightbox/dist/vue-cool-lightbox.min.css";
import { skill, projects } from "../../core/constant";
import { mapState } from "vuex";
import emailjs from "emailjs-com";
import Card from "../common/card.vue";

export default {
  data() {
    return {
      index: null,
      hoverType: "",
      showHover: false,
      skills: [],
      showBackToTop: false,
      section2OffsetTop: null,
      section3OffsetTop: null,
      section4OffsetTop: null,
      activeSkill: null,
      projects: [],
      active: false,
      selectedProject: {},
      activeProject: null,
      isLoading: false,
    };
  },
  computed: {
    ...mapState(["isDark"]),
  },
  created() {
    AOS.init();
  },
  components: {
    Card,
    CoolLightBox,
    Hooper,
    Slide,
    HooperNavigation,
  },
  mounted() {
    window.addEventListener("scroll", this.onScroll);
    this.section2OffsetTop = document.getElementById("section-2").offsetTop;
    this.section3OffsetTop = document.getElementById("section-3").offsetTop;
    this.section4OffsetTop = document.getElementById("section-4").offsetTop;

    this.activeSkill = "language";
    this.activeProject = "All";
  },
  watch: {
    activeSkill(value) {
      this.changeSkill(value);
    },
    activeProject(value) {
      this.changProject(value);
    },
  },
  methods: {
    async sendEmail(e) {
      this.isLoading = true;
      emailjs
        .sendForm("gmail", "template_oFhms863", e.target, "user_XLMXJVmwgbfKoGmzBPLoF")
        .then(
          () => {
            this.isLoading = false;
            this.$noti.display(
              "Successfully Sent To Sai Htun Lu.",
              "Success",
              "normal",
              "success"
            );
          },
          (err) => {
            this.isLoading = false;
            this.$noti.display(err, "Error", "error", "danger");
          }
        );
    },
    seeMorePJ(project) {
      this.active = true;
      this.selectedProject = project;
    },
    changeSkill(type) {
      this.skills = skill.filter((data) => {
        return data.type === type;
      });
    },
    changProject(type) {
      if (type === "All") {
        this.projects = projects;
      } else {
        this.projects = projects.filter((data) => {
          return data.type === type;
        });
      }
    },
    scrollToTech() {
      window.scrollTo(0, this.section2OffsetTop - 60);
    },
    scrollToPj() {
      window.scrollTo(0, this.section3OffsetTop - 30);
    },
    scrollToCt() {
      window.scrollTo(0, document.body.scrollHeight);
    },
    mouseOver(type) {
      this.hoverType = type;
      this.showHover = true;
    },
    mouseLeave() {
      this.showHover = false;
    },
    scrollToTop() {
      window.scrollTo(0, 0);
    },
    onScroll(e) {
      if (600 < e.target.scrollingElement.scrollTop) {
        this.showBackToTop = true;
      } else {
        this.showBackToTop = false;
      }
    },
  },
};
</script>

<style scoped></style>
